body
{
 width:100%;
 margin:0 auto;
 padding:0px;
 font-family:helvetica;
}
.img-main {
    width:100vw;
    height:100vh;
}

.main-container{
    display: flex;
    flex-direction: row;

}
.output-box{
    width: 100vw;
    height: 100vh;
    background-color: #D8D8D8;
}
.wrapper
{
    margin:0 auto;
    padding:10px;
    justify-content: center;
    text-align: center;
    position: absolute;
    height:100%;
    width: 40%;
    right:0;
    top:0;
    background-color: white;
}
#input_image
{
 width: 100%;
 height: 300px;
 border-radius: 5px;
}
.btn-container{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
}
input[type="file"] {
    display: none;
}
.btn{
    font-weight: 600;
    border-radius: 5px;
    padding: 20px 0px;
    border: 1px solid #f53f85;
    background-color: #f53f85;
    font-size: 20px;
    color: white;
    width: 50%;
    margin-top: 50px;
    transition: 0.3s;
}
.fil{
    background-color: transparent;
    color: black;
    
}
.upload:hover{
    background-color: #be2f66;
}
.fil:hover{
    background-color: #f53f85;
    color: white;
}